import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.js';
import { useEffect,useState } from 'react';

const Template =(props)=>{

    const [isNavCollapsed, setIsNavCollapsed] = useState(true);

    const handleNavCollapse = () => {

        setIsNavCollapsed(!isNavCollapsed);

    }

    useEffect(()=>{

        //  if(!isNavCollapsed)
        //  {
        //     if(document.getElementById("navbarSupportedContent")==null)
        //     {
        //         return;
        //     }
        //         document.getElementById("navbarSupportedContent").classList.remove("show");
                 
        //  }

    },[isNavCollapsed]);

    useEffect(()=>{

        //console.log(props);

    },[]);

    

    return (<div>

        <nav className="navbar navbar-expand-lg navbar-dark bg-success fixed-top">
            <div className="container-fluid">
                <a className="navbar-brand" style={{fontSize:"14px"}} href="#">ระบบการรับสมัครนักเรียนโรงเรียนกีฬากรุงเทพมหานคร</a>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" 
                    // aria-expanded={!isNavCollapsed ? true : false}
                     aria-expanded="true"
                    onClick={handleNavCollapse}
                    aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                {/* <div  className={`${isNavCollapsed  ? 'show' : ''} collapse navbar-collapse text-right`}   id="navbarSupportedContent"> */}
                <div  className={` navbar-collapse text-right`}   id="navbarSupportedContent" style={{display:(isNavCollapsed)?"none":""}}>
                   
                    <ul className="navbar-nav ms-auto mb-2 mb-lg-0">

                        <li className="nav-item">
                            <a className="nav-link active" aria-current="page" href="#/register">สมัครเรียน</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link active" >ดูผลการสมัครเรียน</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link active" >กำหนดการ</a>
                        </li>

                        <li className="nav-item">
                            <a className="nav-link active" href="#/admin">สำหรับผู้ดูแลระบบ</a>
                        </li>
                       
                        
                    </ul>
                
                </div>
            </div>
        </nav>
        {(props.header!=null)?<img src={props.header} className="mt-5" style={{width:"100%"}} />:null}
        <div className="container mt-5  " >
            {(props.header==null)?<br />:null}
            {(props.header==null)?<br />:null}
            {props.children}
        </div>

        
        

    </div>);
};

export default Template;