export var server_url=function()
{ 
    if(document.URL.indexOf("localhost")!=-1)
    {
        return "https://localhost/athlete_recruitment/";
    }
    else
    {
        if(document.URL.indexOf("www"==-1))
        {
            return "https://webapp.work/recruitment/";
        }
        else
        {
            return "https://www.webapp.work/recruitment/";
        }
    }
}

