import Template from "./template";
import '@fortawesome/fontawesome-free/css/all.css';
import { useHistory } from "react-router-dom";

const Home=()=>{

    const history=useHistory();
    return (<>
    
        <Template header="img/bannerSportsSchool.jpg">
            
            <div className="row text-center">

                <div className="col-sm-4 mb-2">
                    <button className="btn btn-danger" style={{width:"80%"}} type="button" onClick={event=>{ history.push("/register") }}> <i className='fa fa-pencil-alt'></i> สมัครเรียน</button>
                    
                </div>

                <div className="col-sm-4 mb-2">
                    <button className="btn btn-danger" style={{width:"80%"}} type="button"> <i className='fa fa-file'></i> ดูผลการสมัคร</button>
                </div>

                <div className="col-sm-4 mb-2">
                    <button className="btn btn-danger" style={{width:"80%"}} type="button"> <i className='fa fa-calendar-alt'></i> กำหนดการ</button>
                </div>

                {/* <div className="d-grid gap-2">
                        <button className="btn btn-danger" type="button" onClick={event=>{ history.push("/register") }}> <i className='fa fa-pencil-alt'></i> สมัครเรียน</button>
                        <button className="btn btn-danger" type="button"> <i className='fa fa-file'></i> ดูผลการสมัคร</button>
                        <button className="btn btn-danger" type="button"> <i className='fa fa-calendar-alt'></i> กำหนดการ</button>
                </div> */}

            </div>

            <br />
            <br />
            <br />

        </Template>    
    </>);
}

export default Home;