import { useEffect, useState } from 'react';
import axios from 'axios';
import {loading_button,reset_button} from './lib';
import { server_url } from './config';
const Upload=(props)=>{

    const [my_file,set_file]=useState(null);
    const [btn,set_btn]=useState(null);

    useEffect(()=>{

        if(my_file==null)
        {
            return;
        }
        let formData = new FormData();
        formData.append('my_file', my_file);
        
        if(btn!=null)
        {
            loading_button(btn);   
        }
        let form_data=new FormData();
        form_data.append("my_file",my_file);
        form_data.append("mode","upload");

        let obj=[];
        obj['Content-Type']='multipart/form-data';

        axios.post(server_url()+"api/upload.php",form_data,obj).then(response=>{

            //console.log(response.data);
           
            reset_button(btn);
            if(props.refresh!=null)
            {
                props.refresh(response.data)
            }

        });

    },[my_file]);

   


    return (<div  className="d-grid gap-2">

 
            <button className="btn btn-sm btn-primary  mt-2 "  type="button" data-loading-text="xxx" onClick={event=>{
                set_btn(event.currentTarget);
                event.currentTarget.nextSibling.click();
            }}>
                <i className="fa fa-file"></i>&nbsp;{props.label} 
            </button>
            <input style={{display:"none"}} type="file" onChange={event=>set_file(event.target.files[0])} accept="image/*" />
                     

    </div>)
};

export default Upload;